<template>
  <div class="memorial-sidebar__details">
    <Story
        v-if="isShowStory"
        :storyId="showStoryId"
        :editVersion="showStoryEditVersion"
        :isVisible="showStoryVisible"
        @close="closeModal"
        @approve="approveStory"
    />

    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__switcher">
          <div class="memorial-sidebar__switcher-title">{{ $ml.get('editMemorial.sidebar.relatives.showBlock.label') }}</div>
          <div class="memorial-sidebar__switcher-wrapper">


            <div
                :class="['memorial-sidebar__switcher-box', {'switch': !isVisible}]"
                @click="saveRelativesVisible"
            >
              <div class="memorial-sidebar__switcher-point"></div>
            </div>
            <span>{{ $ml.get('editMemorial.sidebar.about.showBlock.showBtn') }}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__relatives">
          <div class="relatives__controls">
            <div class="relatives__label">
              <div class="label">{{ $ml.with('name', memorial.name).get('editMemorial.sidebar.relatives.editBlock.label') }}
                <span class="icon icon__info info" @click="isTooltipShow = !isTooltipShow"></span>
              </div>
              <div class="description">
                {{ $ml.get('editMemorial.sidebar.relatives.editBlock.description') }}
              </div>
            </div>

            <Button
                :classList="['btn', 'btn__create', 'relatives__add']"
                @clicked="addStory"
                :label="$ml.get('editMemorial.sidebar.relatives.editBlock.btn')"
            >
              <span slot="icon-left" class="icon icon__plus icon__plus icon"></span>
            </Button>
          </div>

          <div class="relatives__items">
            <div
                class="memorial-sidebar__card"
                v-for="story in stories"
                :key="story.id"
            >

              <div class="memorial-sidebar__card-top">
                <div class="memorial-sidebar__user">
                  <div class="memorial-sidebar__user-img">
                    <img v-if="null !== story.owner && null !== story.owner.user.profilePhoto && typeof story.owner.user.profilePhoto.url !== 'undefined'" :src="story.owner.user.profilePhoto.url" alt="photo">
                    <img v-else src="~@/assets/img/icon/relative-placeholder.svg" alt="photo">
                  </div>
                  <div class="memorial-sidebar__user-box">
                    <div class="memorial-sidebar__user-name">{{ story.owner.user.name }}</div>
                    <div class="memorial-sidebar__society">{{ story.owner.relationToMemorialPerson }}</div>
                  </div>
                </div>

                <button
                    class="memorial-sidebar__card-btn"
                    @click.prevent.stop="showStory(story)"
                >
                  {{ $ml.get('editMemorial.sidebar.relatives.editBlock.story.btn') }}
                </button>
              </div>

              <div class="memorial-sidebar__card-bottom">
                <div
                    class="memorial-sidebar__switcher-wrapper memorial-sidebar__switcher-wrapper--mod memorial-sidebar__switcher-wrapper--indent-2"
                    @click.prevent.stop="showStory(story)"
                >
                  <img src="~@/assets/img/icon/post-icon.svg" alt="">
                  <span v-html="story.period.name"></span>
                </div>
                <div
                    class="memorial-sidebar__switcher-wrapper memorial-sidebar__switcher-wrapper--mod memorial-sidebar__switcher-wrapper--indent"
                    @click.prevent.stop="changeStoryVisible(story)"
                >
                  <div :class="['memorial-sidebar__switcher-box', {'switch': !story.visible}]">
                    <div class="memorial-sidebar__switcher-point"></div>
                  </div>
                  <span v-if="story.visible">{{ $ml.get('editMemorial.sidebar.relatives.editBlock.story.show') }}</span>
                  <span v-else>{{ $ml.get('editMemorial.sidebar.relatives.editBlock.story.hide') }}</span>
                </div>
              </div>
            </div>

          </div>

        </div>
      </div>
    </div>

    <Tooltip
        v-if="isTooltipShow"
        :label="$ml.get('editMemorial.sidebar.relatives.help.label')"
        :withTalkLink="false"
        @close="isTooltipShow = false"
    >
      <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get('editMemorial.sidebar.relatives.help.text') }}
        </span>
      </div>
    </Tooltip>
  </div>
</template>

<script>
import Button from "@/components/Button";
import Modal from "@/components/CreatePeriodModal";
import Story from "@/views/dashboard/Story";
import Tooltip from "@/components/Tooltip";
import {mapGetters} from "vuex";

export default {
  name: "Relatives",
  components: {
    Button,
    Modal,
    Story,
    Tooltip,
  },
  props: {
    memorial: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isTooltipShow: false,
      description: '',
      isVisible: false,
      isShowPeriods: false,
      isSwitching: false,

      isShowStory: false,
      showStoryId: null,
      showStoryEditVersion: null,
      showStoryVisible: false,
      storyVisibleSwitching: [],
    };
  },
  methods: {
    async saveRelativesVisible() {
      if(!this.isSwitching) {
        this.isSwitching = true;
        this.isVisible = !this.isVisible;
        const payload = {
          memorialId: this.memorial.id,
          data: {
            'editVersion': this.memorial.editVersion,
            'relativesStoriesVisible': this.isVisible,
          },
        };

        await this.$store.dispatch('dashboard/updateFields', payload);
        this.isSwitching = false;
        this.$emit('updated');
      }
    },
    async changeStoryVisible(story) {
      if(!this.storyVisibleSwitching.includes(story.id)) {
        this.storyVisibleSwitching.push(story.id);
        const payload = {
          id: story.id,
          data: {
            editVersion: story.editVersion,
            visible: !story.visible,
          },
        };
        const response = await this.$store.dispatch('story/setVisible', payload);

        await this.$store.dispatch('story/updateEditStories', response);

        let idx = this.storyVisibleSwitching.indexOf(story.id);
        delete this.storyVisibleSwitching[idx];

        this.$emit('updated');
      }
    },
    addStory() {
      this.$router.push({name: 'memorials.story.auth', params: {slug: this.memorial.id}});
    },
    showStory(story) {
      this.isShowStory = true;
      this.showStoryId = story.id;
      this.showStoryEditVersion = story.editVersion;
      this.showStoryVisible = story.visible;
    },
    closeModal() {
      this.isShowStory = false;
      this.showStoryId = null;
      this.showStoryEditVersion = null;
      this.showStoryVisible = false;
      this.$store.dispatch('story/saveStory', {});
    },
    async approveStory() {
      this.closeModal();
    },
  },
  computed: {
    ...mapGetters({
      periods: 'period/getPeriods',
      stories: 'story/editStories',
    }),
  },
  async mounted() {
    await this.$store.dispatch('period/getPeriods', { id: this.memorial.id, storyType: 'all', withGeneral: false });

    this.isVisible = this.memorial.relativesStoriesVisible;

    await this.$store.dispatch('story/getEditStories', { id: this.memorial.id, });
  },
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/components/sidebars/relatives.scss";
</style>