<template>
  <div class="memorial-sidebar__details">
    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__switcher">
          <div class="memorial-sidebar__switcher-title">{{ $ml.get('editMemorial.sidebar.periods.showBlock.label') }}</div>
          <div class="memorial-sidebar__switcher-wrapper">


            <div
                :class="['memorial-sidebar__switcher-box', {'switch': !isVisible}]"
                @click="savePeriodVisible"
            >
              <div class="memorial-sidebar__switcher-point"></div>
            </div>
            <span>{{ $ml.get('editMemorial.sidebar.about.showBlock.showBtn') }}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__periods">
          <div class="periods__controls">
            <div class="periods__label">
              <div class="label">{{ $ml.with('name', memorial.name).get('editMemorial.sidebar.periods.editBlock.label') }}
                <span class="icon icon__info info" @click="isTooltipShow = !isTooltipShow"></span>
              </div>
              <div class="description">
                {{ $ml.with('name', memorial.name).get('editMemorial.sidebar.periods.editBlock.description') }}
              </div>
            </div>

            <Button
                :classList="['btn', 'btn__light', 'periods__add']"
                :label="$ml.get('editMemorial.sidebar.periods.editBlock.btn')"
                @clicked="addPeriod"
            >
              <span slot="icon-left" class="icon icon__plus icon__plus-blue icon"></span>
            </Button>
          </div>

          <div class="periods__items">
            <div class="periods__item"
                 v-for="period in periods"
                 :key="period.id"
                 @click="goToEdit(period)"
            >
              <div class="periods__logo">
                <img src="~@/assets/img/icon/general-period.svg" alt="" v-if="period.isGeneral">
                <img src="~@/assets/img/icon/calendar.svg" alt="" v-else-if="!period.isGeneral && null !== period.since && null !== period.till">
                <img src="~@/assets/img/icon/label.svg" alt="" v-else-if="!period.isGeneral && null === period.since && null === period.till">
              </div>

              <div class="periods__info">
                <span class="periods__name" v-html="period.name"></span>
                <div class="periods__date" v-if="null !== period.since && null !== period.till">
                  <span class="since">{{ period.since | getYear }}</span><span class="sep"> -</span> <span class="till">{{ period.till | getYear }}</span>
                </div>
              </div>
              
              <span class="icon icon__arrow-2 arrow-icon"></span>
            </div>
          </div>

        </div>
      </div>
    </div>

    <Modal
        v-if="isShowPeriods"
        :memorial="memorial"
        @close="closeModalPeriod"
        @updatePeriods="updatePeriods"
    />

    <Tooltip
        v-if="isTooltipShow"
        :label="$ml.get('editMemorial.sidebar.life.help.label')"
        :withTalkLink="false"
        @close="isTooltipShow = false"
    >
      <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get('editMemorial.sidebar.life.help.text') }}
        </span>
      </div>
    </Tooltip>
  </div>
</template>

<script>
import Button from "@/components/Button";
import Modal from "@/components/CreatePeriodModal";
import {mapGetters} from "vuex";
import Tooltip from "@/components/Tooltip";

export default {
  name: "Periods",
  props: {
    memorial: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  components: {
    Button,
    Modal,
    Tooltip,
  },
  data() {
    return {
      isTooltipShow: false,
      isVisible: false,
      isShowPeriods: false,
      isSwitching: false,
    };
  },
  methods: {
    async savePeriodVisible() {
      if(!this.isSwitching) {
        this.isSwitching = true;
        this.isVisible = !this.isVisible;
        const payload = {
          memorialId: this.memorial.id,
          data: {
            'editVersion': this.memorial.editVersion,
            'lifeStoriesVisible': this.isVisible,
          },
        };

        await this.$store.dispatch('dashboard/updateFields', payload);
        this.isSwitching = false;
        this.$emit('updated');
      }
    },
    addPeriod() {
      this.isShowPeriods = true;
    },
    closeModalPeriod() {
      this.isShowPeriods = false;
    },
    async updatePeriods() {
      await this.$store.dispatch('period/getPeriods', { id: this.memorial.id, storyType: 'all', withGeneral: false });
    },
    goToEdit(period) {
      this.$router.push({name: 'memorials.edit.life', params: { id: this.memorial.id, period: period.id }});
    },
  },
  computed: {
    ...mapGetters({
      periods: 'period/getPeriods',
    }),
  },
  async mounted() {
    await this.$store.dispatch('period/getPeriods', { id: this.memorial.id, storyType: 'all', withGeneral: false });

    this.isVisible = this.memorial.lifeStoriesVisible;
  },
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/utils/mixins.scss";
  @import "~@/assets/scss/components/sidebars/periods.scss";
</style>