<template>
  <div :class="['edit-memorial', {'editing': isEditing}]">

    <Header
        :user="user"
        @changeLanguage="changeLanguage"
        @changePlatform="changePlatform"
        @setEditingBlock="openEdit"
    />

    <div class="edit-memorial__content">
      <StorySidebar
          v-if="isEditing"
          :isEditing="isEditing"
          :title="sidebarTitle"
          :editBlock="editBlock"
          @close="closeSidebar"
          @setEditingBlock="openEdit({block: 'default'})"
      >
        <component
            slot="sidebar-content"
            :is="`${editBlock}-sidebar`"
            :memorial="memorial"
            @updated="updateMemorial"
            @setEditingBlock="openEdit"
            :key="memorial.id"
        />
      </StorySidebar>

      <div class="edit-memorial__wrapper">
        <div :class="['edit-memorial__container', platform, {'sidebar-open': isEditing}]" id="edit-memorial" :dir="'he' === lang ? 'rtl' : 'ltr'">
        <section
            ref="main"
            :class="['card-info', 'edit-memorial__block', 'profile', {'card-info--mod': !memorial.profilePhotoFullScreen}, {'edit-memorial__block-disabled': isEditing && !['common', 'main'].includes(editBlock)}]"
            :style="{backgroundImage: !memorial.profilePhotoFullScreen ? `url(${cover})` : ''}"
        >
          <div class="edit-memorial__overlay" v-if="isEditing && !['common', 'main'].includes(editBlock)"></div>

          <div v-if="memorial" class="full-card edit-memorial__block edit-memorial__card profile profile-padding" :class="{'reserve-bg': !memorial.profilePhoto}"
               :style="{backgroundImage: memorial.profilePhotoFullScreen && memorial.profilePhoto
                 ? `url(${memorial.profilePhoto.url})`
                 : (
                     !memorial.profilePhotoFullScreen ? `url(${cover})` : ''
                 )}"
          >
            <div class="card-info__mask"></div>

            <div class="profile__header">
              <div class="profile__nav">
                <span class="nav__item name" v-html="memorial.name"></span>
                <span class="nav__item">{{ $ml.get(`editMemorial.${lang}.header.life`) }}</span>
                <span class="nav__item">{{ $ml.get(`editMemorial.${lang}.header.relatives`) }}</span>
              </div>

              <div :class="['mobile-menu__hamburger']">
                <div :class="['hamburger']">
                  <span></span>
                  <span></span>
                </div>
              </div>
              <div class="profile__header-wrapper"><span class="profile__header-name">{{ memorial.name }}</span>
              <span class="profile__header-sub">{{ memorial.birthDate | getYear }} - {{ memorial.deathDate | getYear }}</span></div>

              <span class="profile__logo"></span>
            </div>

            <img class="full-card__top-arrow" src="~@/assets/img/icon/card-arrow.svg" alt="" href="#about"
                 v-smooth-scroll>
            <div class="full-card__empty"></div>

            <div class="full-card__main">
              <div class="full-card__name">
                {{ memorial.name }}<span>{{ $ml.get('create.memorial.sidebar.rip') }}</span>
              </div>
              <div class="full-card__foto-years full-card__foto-years--mod">
                {{ memorial.birthDate | getYear }} - {{ memorial.deathDate | getYear }}
              </div>
            </div>

            <div class="full-card__middle">
              <div class="full-card__middle-image">
                <img :src="memorial.profilePhoto ? memorial.profilePhoto.url : require('@/assets/img/reserve-bg.png')" alt="">
              </div>
              <!--                <div class="full-card__middle-title">אתר הנצחה</div>-->
              <div class="full-card__middle-name">
                {{ memorial.name }}<span>{{ $ml.get('create.memorial.sidebar.rip') }}</span>
              </div>
              <div class="full-card__middle-foto-years">
                {{ memorial.birthDate | getYear }} - {{ memorial.deathDate | getYear }}
              </div>
            </div>

            <!-- full-card__footer -->
            <div class="full-card__footer" v-if="stories.length && memorial.showQuote">
              <div class="full-card__footer-wrapper" v-if="stories.length">
                <div class="full-card__footer-small-foto">
                  <router-link :to="{ name: 'memorials.relatives', params: { slug: memorial.slug, relative: stories[0].owner.user.id } }">
                    <img :src="stories[0].owner.user.profilePhoto ? stories[0].owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                         alt="Foto">
                  </router-link>
                  <div class="full-card__footer-foto-caption">
                    <span>{{ stories[0].owner.user.name }}</span>
                    {{ stories[0].owner.relationToMemorialPerson }}
                  </div>
                </div>

                <div class="full-card__footer-left">
                  <div class="full-card__footer-quotes">
                    <img src="~@/assets/img/icon/card-quotes.svg" alt="Quotes">
                  </div>
                  <div class="full-card__footer-text" v-html="stories[0].description">
                  </div>
                </div>
              </div>
            </div>
          </div>

          <Button
              :classList="['btn', 'btn__primary-2', 'btn-edit']"
              @clicked="edit('main')"
              :label="$ml.get(`editMemorial.${lang}.edit`)"
          >
            <span slot="icon-left" class="icon icon__edit edit-icon"></span>
          </Button>

        </section>

        <!-- about-person -->
        <section
            ref="person"
            v-if="memorial.descriptionVisible"
            :class="['about-person', 'edit-memorial__block', 'person', {'edit-memorial__block-disabled': isEditing && !['common', 'person'].includes(editBlock)}]"
        >
          <div class="edit-memorial__overlay" v-if="isEditing && !['common', 'person'].includes(editBlock)"></div>
          <div class="container">
            <div class="about-person__container">
              <h3 class="about-person__name second-header edit-memorial__label">{{ $ml.with('name', memorial.name).get('memorial.about.title') }}</h3>
              <p class="about-person__info info-text" v-if="memorial.description" v-html="memorial.description"></p>
              <div v-else class="edit-memorial__placeholder-description">
                <span class="label">{{ $ml.with('name', memorial.name).get('editMemorial.sidebar.about.descriptionBlock.label') }}</span>

                <Button
                    :classList="['btn', 'btn__create', 'add-btn']"
                    :label="$ml.get('memorial.addDescriptionBtn')"
                >
                  <span slot="icon-left" class="icon icon__plus icon__plus icon-add"></span>
                </Button>
              </div>
            </div>
          </div>

          <Button
              :classList="['btn', 'btn__primary-2', 'btn-edit']"
              @clicked="edit('person')"
              :label="$ml.get(`editMemorial.${lang}.edit`)"
          >
            <span slot="icon-left" class="icon icon__edit edit-icon"></span>
          </Button>
        </section>

        <!-- life-story -->
        <section
            v-if="memorial.lifeStoriesVisible"
            ref="life-story"
            :class="['life-story', 'edit-memorial__block', 'life-story', {'edit-memorial__block-disabled': isEditing && !['common', 'life-story'].includes(editBlock)}]"
        >
          <div class="edit-memorial__overlay" v-if="isEditing && !['common', 'life-story'].includes(editBlock)"></div>
          <!-- <div class="container"> -->
          <div :class="['edit-memorial__stories', {'placeholder': !periods.length}]">
            <div class="life-story__title-box">
              <div class="life-story__title second-header edit-memorial__label">{{ $ml.get(`editMemorial.${lang}.life.title`) }}</div>
              <p class="life-story__timeline info-text">{{ $ml.with('name', memorial.name).get('memorial.life.desc') }}</p>
            </div>
            <!-- life-slider -->

            <div class="edit-memorial__placeholder-life" v-if="!periods.length">
              <Button
                  :classList="['btn', 'btn__create', 'add-btn']"
                  :label="$ml.get('memorial.addDescriptionBtn')"
              >
                <span slot="icon-left" class="icon icon__plus icon__plus icon-add"></span>
              </Button>
            </div>

            <div v-if="showPeriods && periods.length"
                 :class="[
                     'swiper-container',
                      'swiper',
                       'life-story__slider',
                       'life-slider',
                        {'langEnHe': $ml.current === 'en' && lang === 'he'},
                        {'langHeEn': $ml.current === 'he' && lang === 'en'}
                        ]" :dir="'he' === lang ? 'rtl' : 'ltr'">

              <div :class="['swiper-wrapper']">
                <!-- Slides -->
                <div class="swiper-slide" v-for="period in periods" :key="period.id">
                  <div class="life-slider__item">
                    <div class="life-slider__item-img-box">
                      <div class="life-slider__item-mask"></div>
                      <img :src="getPeriodImage(period)"
                           alt="" class="life-slider__img">
                    </div>
                    <span class="period-name" v-html="period.name"></span>
                    <div class="life-slider__item-dot"></div>
                    <div class="life-slider__item-years" v-if="period.since">{{ period.since | getYear }} - {{ period.till | getYear }}</div>
                  </div>
                </div>
              </div>

              <div class="swiper-button-prev swiper-button" slot="button-prev">
                <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                     viewBox="0 0 18.787 18.787">
                  <path id="Path_1969" data-name="Path 1969"
                        d="M22.787,12.219H8.5l6.564-6.564L13.393,4,4,13.393l9.393,9.393,1.656-1.656L8.5,14.568h14.29Z"
                        transform="translate(-4 -4)" fill="currentColor" />
                </svg>
              </div>
              <div class="swiper-button-next swiper-button" slot="button-next">
                <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                     viewBox="0 0 18.787 18.787">
                  <path id="Path_1969" data-name="Path 1969"
                        d="M18.787,8.219H4.5l6.564-6.564L9.393,0,0,9.393l9.393,9.393,1.656-1.656L4.5,10.568h14.29Z"
                        fill="currentColor" />
                </svg>
              </div>
            </div>

            <div class="life-story__box-btn" v-if="periods.length">
              <Button
                  :classList="['btn', 'btn__primary-2', 'life-story__btn']"
                  :label="$ml.get(`editMemorial.${lang}.life.button`)"
              />
            </div>
          </div>

          <Button
              :classList="['btn', 'btn__primary-2', 'btn-edit']"
              @clicked="edit('life-story')"
              :label="$ml.get(`editMemorial.${lang}.edit`)"
          >
            <span slot="icon-left" class="icon icon__edit edit-icon"></span>
          </Button>
        </section>

        <!-- family -->
        <section v-if="memorial.relativesStoriesVisible" ref="relative-story" :class="['family', 'edit-memorial__block', 'relative-story', {'edit-memorial__block-disabled': isEditing && !['common', 'relative-story'].includes(editBlock)}]">
          <div class="edit-memorial__overlay" v-if="isEditing && !['common', 'relative-story'].includes(editBlock)"></div>
          <div class="container">
            <div class="family__title-wrapper">
              <h3 class="second-header edit-memorial__label">{{ $ml.get(`editMemorial.${lang}.relatives.title`) }}</h3>
              <p class="info-text">{{ $ml.with('name', memorial.name).get('memorial.relatives.desc') }}</p>
            </div>

            <div class="edit-memorial__placeholder-relative" v-if="!stories.length">
              <span class="placeholder-3"></span>
              <span class="placeholder-2"></span>
              <span class="placeholder-1"></span>
              <Button
                  :classList="['btn', 'btn__create', 'add-btn']"
                  :label="$ml.get('memorial.addDescriptionBtn')"
              >
                <span slot="icon-left" class="icon icon__plus icon__plus icon-add"></span>
              </Button>
            </div>

            <div class="family__container" v-if="stories.length">

              <div class="family__col last" v-if="stories.length > 4">
                <div v-for="(story, key) in stories" :key="'third-col-'+story.id">
                  <div v-if="key === 4 || key === 5">
                    <div v-if="story.content.length && story.content[0].contentType === 'audio'">
                      <AudioStory
                          :item="story.content[0]"
                          :owner="story.owner"
                          :key="story.content[0].id"
                          :addClass="['family__qoute']"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].contentType === 'video'">
                      <VideoStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].contentType === 'image'">
                      <ImageStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].type === 'link'">
                      <ImageStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else>
                      <TextStory
                          :story="story"
                          :key="story.id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                  </div>
                </div>
              </div>


              <div class="family__col family__col--mb" v-if="stories.length > 2">
                <div v-for="(story, key) in stories" :key="'second-col-'+story.id">
                  <div v-if="key === 2 || key === 3">
                    <div v-if="story.content.length && story.content[0].contentType === 'audio'">
                      <AudioStory
                          :item="story.content[0]"
                          :key="story.content[0].id"
                          :owner="story.owner"
                          :addClass="['family__qoute']"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].contentType === 'video'">
                      <VideoStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].contentType === 'image'">
                      <ImageStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].type === 'link'">
                      <ImageStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else>
                      <TextStory
                          :story="story"
                          :key="story.id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                  </div>
                </div>
              </div>

              <div class="family__col first">
                <div v-for="(story, key) in stories" :key="'first-col-'+story.id">
                  <div v-if="key <= 1">
                    <div v-if="story.content.length && story.content[0].contentType === 'audio'">
                      <AudioStory
                          :item="story.content[0]"
                          :key="story.content[0].id"
                          :owner="story.owner"
                          :addClass="['family__qoute']"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].contentType === 'video'">
                      <VideoStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].contentType === 'image'">
                      <ImageStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else-if="story.content.length && story.content[0].type === 'link'">
                      <ImageStory
                          :story="story"
                          :key="story.content[0].id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                    <div v-else>
                      <TextStory
                          :story="story"
                          :key="story.id"
                          :memorialSlug="memorial.slug"
                      />
                    </div>
                  </div>
                </div>
              </div>

            </div>

            <div class="family__button-container" v-if="stories.length">
              <button type="button" class="family__button default-btn">{{ $ml.get(`editMemorial.${lang}.relatives.button`) }}</button>
            </div>
          </div>

          <Button
              :classList="['btn', 'btn__primary-2', 'btn-edit']"
              @clicked="edit('relative-story')"
              :label="$ml.get(`editMemorial.${lang}.edit`)"
          >
            <span slot="icon-left" class="icon icon__edit edit-icon"></span>
          </Button>
        </section>

        <section class="page-footer edit-memorial__footer">
          <div class="container">
            <div class="page-footer__title-wrapper">
              <div class="page-footer__logo">
                <img src="~@/assets/img/icon/footer-logo.svg" alt="">
              </div>
              <p class="page-footer__title-text">{{ $ml.get(`editMemorial.${lang}.footer.text`) }}</p>
            </div>

            <div class="page-footer__bottom">
              <div class="page-footer__privacy-box">
                <a><img src="~@/assets/img/icon/footer-flag.svg" alt="">{{ $ml.get(`editMemorial.${lang}.footer.link3`) }}</a>
                <a>{{ $ml.get(`editMemorial.${lang}.footer.link2`) }}</a>
                <a>{{ $ml.get(`editMemorial.${lang}.footer.link1`) }}</a>
              </div>

              <div class="page-footer__btn-box">
                <button class="page-footer__btn">{{ $ml.get(`editMemorial.${lang}.footer.button1`) }}</button>
                <button class="page-footer__btn">{{ $ml.get(`editMemorial.${lang}.footer.button2`) }}</button>
              </div>


              <div class="page-footer__rights">
                {{ $ml.with('year', new Date().getFullYear()).get(`editMemorial.${lang}.footer.copy`) }}
              </div>


            </div>
          </div>
        </section>
      </div>
      </div>
    </div>

    <HelpCenter
      :withStoryBtn="true"
      :memorialSlug="memorial.id"
    />
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import Swiper, { Navigation } from 'swiper';
Swiper.use([Navigation]);
import { EventBus } from '@/utils/EventBus.js';

import Header from './partials/Header';
import RelativeStorySidebar from '@/components/stories/sidebars/Relatives';
import PersonSidebar from '@/components/stories/sidebars/About';
import DefaultSidebar from '@/components/stories/sidebars/Default';
import MainSidebar from '@/components/stories/sidebars/Main';
import LifeStorySidebar from '@/components/stories/sidebars/Periods';
import StorySidebar from '@/components/StorySidebar';
import Button from '@/components/Button';
import AudioStory from '@/components/stories/AudioStory';
import TextStory from '@/components/stories/TextStory';
import VideoStory from '@/components/stories/VideoStory';
import ImageStory from '@/components/stories/ImageStory';
import HelpCenter from '@/components/HelpCenter.vue';

export default {
  name: "MemorialsEdit",
  components: {
    Header,
    StorySidebar,
    PersonSidebar,
    DefaultSidebar,
    LifeStorySidebar,
    RelativeStorySidebar,
    MainSidebar,
    Button,
    AudioStory,
    TextStory,
    VideoStory,
    ImageStory,
    HelpCenter,
  },
  data() {
    return {
      showPeriods: true,
      isEditing: false,
      editBlock: '',
      swiperOptions: {
        direction: 'horizontal',
        loop: false,
        // slidesPerView: 3,
        spaceBetween: 20,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        breakpoints: {
          // when window width is >= 320px
          // 320: {
          //   slidesPerView: 1.3,
          //   spaceBetween: 20
          // },

          375: {
            slidesPerView: 1.35,
          },
          385: {
            slidesPerView: 1.4,
          },
          410: {
            slidesPerView: 1.5,
          },
          425: {
            slidesPerView: 1.5,
          },
          440: {
            slidesPerView: 1.6,
          },
          470: {
            slidesPerView: 1.7,
          },
          495: {
            slidesPerView: 1.8,
          },
          520: {
            slidesPerView: 1.9,
          },
          545: {
            slidesPerView: 2.0,
          },
          570: {
            slidesPerView: 2.1,
          },
          625: {
            slidesPerView: 2.3,
          },
          650: {
            slidesPerView: 2.4,
          },
          700: {
            slidesPerView: 2.6,
          },
          730: {
            slidesPerView: 2.7,
          },
          // 750: {
          //   slidesPerView: 2.7,
          // },
          // 790: {
          //   slidesPerView: 2.7,
          // },
          800: {
            slidesPerView: 2.8,
          },
          840: {
            slidesPerView: 3.0,
          },
          880: {
            slidesPerView: 3.1,
          },
          920: {
            slidesPerView: 3.2,
          },
          960: {
            slidesPerView: 3.4,
          },
          1000: {
            slidesPerView: 3.5,
          },
          1040: {
            slidesPerView: 3.7,
          },
          1080: {
            slidesPerView: 3.8,
          },

          1120: {
            slidesPerView: 4,
          },
          1160: {
            slidesPerView: 4.1,
          },
          1200: {
            slidesPerView: 4.1,
          },
          1240: {
            slidesPerView: 4.3,
          },
          1280: {
            slidesPerView: 4.5,
          }
        }
      },
      lang: 'en',
      platform: 'desktop',
      storiesSlider: null,
      sidebarTitle: '',
      covers: [
        {
          id: '1',
          name: 'האוקיינוס',
          file: require('@/assets/img/full-card-bg2.png'),
        },
        {
          id: '2',
          name: 'פרחים',
          file: require('@/assets/img/memorial-style-item-2.png'),
        },
        {
          id: '3',
          name: 'טרסות',
          file: require('@/assets/img/memorial-style-item-3.png'),
        },
        {
          id: '4',
          name: 'שמים',
          file: require('@/assets/img/memorial-style-item-4.png'),
        },
        {
          id: '5',
          name: 'נחלים',
          file: require('@/assets/img/memorial-style-item-5.png'),
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      memorial: 'dashboard/currentMemorial',
      user: 'profile/getUser',
      periods: 'period/getPeriods',
      stories: 'story/getStories',
      isLoggedIn: 'auth/isLoggedIn',
      token: 'auth/token',
    }),
    cover() {
      let cover = this.covers.find(i => i.id === this.memorial.profilePhotoBackground);

      return cover ? cover.file : '';
    }
  },
  methods: {
    getPeriodImage(period) {
      if(period.thumbnail) {
        return period.thumbnail.url;
      } else {
         if(period.defaultImageName) {
           return require(`@/assets/img/icon/periods/${period.defaultImageName}/1x.png`);
         } else {
           return require('@/assets/img/reserve-bg-cut.png');
         }
      }
    },
    updateMemorial() {

    },
    sliderReInit() {
      this.destroySlider();

      let _this = this;
      setTimeout(() => {
        _this.initSlider();
      }, 2000);
    },
    closeSidebar() {
      this.isEditing = false;
      this.editBlock = '';
      EventBus.$emit('memorial-edit', this.isEditing);
    },
    changeLanguage(lang) {
      this.lang = lang;
      this.destroySlider();
      this.showPeriods = false;
      this.showPeriods = true;
      let _this = this;
      setTimeout(() => {

        _this.initSlider();
      }, 2000);
    },
    changePlatform(platform) {
      this.platform = platform;
    },
    openEdit({block = 'default', isEdit = true}) {
      this.isEditing = isEdit;
      this.editBlock = block;

      if(typeof this.$refs[this.editBlock] !== 'undefined') {
        let _this = this;
        setTimeout(() => {
          _this.$refs[this.editBlock].scrollIntoView({block: "start", behavior: "smooth"});
        }, 1);
      }

      EventBus.$emit('memorial-edit', this.isEditing);
    },
    setEditing() {
      this.edit('common');
    },
    edit(block) {
      if(this.isEditing) {
        this.isEditing = false;
        this.editBlock = '';
      } else {
        this.isEditing = true;
        this.editBlock = block;

        if(typeof this.$refs[this.editBlock] !== 'undefined') {
          let _this = this;
          setTimeout(() => {
            _this.$refs[this.editBlock].scrollIntoView({block: "start", behavior: "smooth"});
          }, 1);
        }
      }

      EventBus.$emit('memorial-edit', this.isEditing);
    },
    isMemorialFieldExist(field) {
      return typeof this.memorial[field] !== 'undefined' && null !== this.memorial[field];
    },
    initSlider() {
      let _this = this;
      setTimeout(() => {
        this.storiesSlider = new Swiper('.swiper-container', this.swiperOptions);
      }, 1000);
    },
    destroySlider() {
      if(this.storiesSlider) {
        this.storiesSlider.destroy(true, true);
      }
    },
  },
  watch: {
    lang(newLang) {
      this.sliderReInit();
    },
    'memorial.slug'(slug) {
      this.$store.dispatch('period/getPeriods', { id: this.memorial.id, storyType: 'all', withGeneral: false });
      this.$store.dispatch('story/getRandomStories', {id: this.memorial.id, count: 6});

      try {
        this.sliderReInit();
      } catch (err) {

      }
    },
    editBlock(block) {
      switch (block) {
        case 'default':
          this.sidebarTitle = this.$ml.get('editMemorial.sidebar.header.title');
          break;
        case 'main':
          this.sidebarTitle = this.$ml.with('name', this.memorial.name).get('editMemorial.sidebar.about.title');
          break;
        case 'person':
          this.sidebarTitle = this.$ml.with('name', this.memorial.name).get('editMemorial.sidebar.about.title');
          break;
        case 'life-story':
          this.sidebarTitle = this.$ml.get('editMemorial.sidebar.periods.title');
          break;
        case 'relative-story':
          this.sidebarTitle = this.$ml.get('editMemorial.sidebar.relatives.title');
          break;
        default:
            this.sidebarTitle = '';
            break;
      }
    }
  },
  async mounted() {
    this.initSlider();

    await this.$store.dispatch('period/getPeriods', { id: this.memorial.id, storyType: 'all', withGeneral: false });
    await this.$store.dispatch('story/getRandomStories', {id: this.memorial.id, count: 6});

    let hash = document.location.hash.replace('#', '');

    if(hash) {
      this.edit(hash);
    }
  },
  async created() {
    this.$store.commit('memorial/setDefaultData');
    this.lang = this.$ml.current;
  },
}
</script>

