<template>
  <div class="memorial-sidebar__details">
    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__details-title">{{ $ml.get('editMemorial.sidebar.main.title') }}</div>
        <form class="memorial-sidebar__form">

          <div class="memorial-sidebar__group" :class="{'invalid': bindClassInvalid('name')}">
<!--            <select name="" id="" class="memorial-sidebar__select">-->
<!--              <option selected="selected" class="selected" value="0"></option>-->
<!--              <option value="1">חבר/ה</option>-->
<!--              <option value="2">בן </option>-->
<!--              <option value="3">בת </option>-->
<!--              <option value="4">אמא </option>-->
<!--              <option value="5">אבא </option>-->
<!--              <option value="6">סבא </option>-->
<!--              <option value="7">סבתא </option>-->
<!--            </select>-->

            <input v-model="model.name" @blur="save('name')" type="text" class="memorial-sidebar__input" required>

            <div class="memorial-sidebar__floating-label">{{ $ml.get('editMemorial.sidebar.main.name') }}</div>
<!--            <div class="memorial-sidebar__select-img"><img src="/img/arrow-down-blue.d930adaa.svg" alt="">-->
<!--            </div>-->

            <div class="memorial-sidebar__error-box">
              <img src="~@/assets/img/icon/error-red.svg" alt="" class="memorial-sidebar__error-img">
              <span class="memorial-sidebar__error">{{ showErrorText('name') }}</span>
            </div>

          </div>

          <div class="memorial-sidebar__group" :class="{'invalid': bindClassInvalid('birthDate')}">
            <img slot="afterDateInput" src="~@/assets/img/icon/calendar-24px.svg"
                 class="memorial-sidebar__form-icon">
            <input type="text" class="memorial-sidebar__input"
                   v-model="birthDate"
                   v-on:keydown.capture.prevent.stop
                   @blur="closeBirthdayPicker"
                   @click.prevent="openBirthdayPicker"
            >
            <div class="memorial-sidebar__floating-label">{{ $ml.get('editMemorial.sidebar.main.birthDate') }}</div>
            <div class="memorial-sidebar__error-box">
              <img src="~@/assets/img/icon/error-red.svg" alt="" class="memorial-sidebar__error-img">
              <span class="memorial-sidebar__error">{{ showErrorText('birthDate') }}</span>
            </div>

            <datepicker
                    ref="birthday"
                    :language="dtLocale"
                    :required="true"
                    :input-class="{'memorial-sidebar__input': true}"
                    id="birthday"
                    class="datepicker"
                    @selected="birthdayPickerSelected"
            >
            </datepicker>

          </div>

          <div class="memorial-sidebar__group" :class="{'invalid': bindClassInvalid('deathDate')}">
            <img slot="afterDateInput" src="~@/assets/img/icon/calendar-24px.svg"
                 class="memorial-sidebar__form-icon">
            <input type="text" class="memorial-sidebar__input"
                   v-model="deathDate"
                   v-on:keydown.capture.prevent.stop
                   @blur="closeDeathPicker"
                   @click.prevent="openDeathPicker"
            >
            <div class="memorial-sidebar__floating-label">{{ $ml.get('editMemorial.sidebar.main.deathDate') }}</div>
            <div class="memorial-sidebar__error-box">
              <img src="~@/assets/img/icon/error-red.svg" alt="" class="memorial-sidebar__error-img">
              <span class="memorial-sidebar__error">{{ showErrorText('deathDate') }}</span>
            </div>

            <datepicker
                    ref="death"
                    :language="dtLocale"
                    :required="true"
                    :input-class="{'inputText form__input': true}"
                    id="death"
                    class="datepicker"
                    @selected="deathPickerSelected"
            >
            </datepicker>
          </div>
        </form>

        <div class="memorial-sidebar__cover">
          <input @change="uploadAvatar" accept="image/*" id="avatar-file" ref="avatar"
                 class="form__file" type="file" style="display: none;">

          <img class="memorial-sidebar__cover-img"
               :src="avatarPreview" alt="">
          <div class="memorial-sidebar__cover-edit">
            <img src="~@/assets/img/icon/memorial-sidebar-edit.svg" alt=""
                 onclick="document.getElementById('avatar-file').click()"
            >
          </div>
        </div>
      </div>
    </div>

    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__style-title">{{ $ml.get('editMemorial.sidebar.main.coverMode') }}</div>
        <div class="memorial-sidebar__style-card-box">
          <!-- при клике класс checked -->
          <div class="memorial-sidebar__style-card-wrapper"
               :class="{'checked': model.profilePhotoFullScreen}"
               @click="model.profilePhotoFullScreen=true;save('profilePhotoFullScreen')"
          >
            <div class="memorial-sidebar__style-card">
              <img :src="avatarPreview" alt="">
            </div>
          </div>


          <!-- при клике класс добавить checked и memorial-sidebar__style-items поменять стили на display: flex; -->
          <div class="memorial-sidebar__style-card-wrapper"
               :class="{'checked': !model.profilePhotoFullScreen}"
               @click="model.profilePhotoFullScreen=false;save('profilePhotoFullScreen')"
          >
            <div class="memorial-sidebar__style-card">
              <img src="~@/assets/img/memorial-style-card.png" alt="">
                <img class="memorial-sidebar__style-card-img" :src="avatarPreview" alt="">
            </div>
          </div>
        </div>

      </div>
    </div>

      <div :class="['memorial-sidebar__item', {'ios': isIOS}]">
          <div class="memorial-sidebar__item-wrapper">
              <div class="memorial-sidebar__switcher">
                  <div class="memorial-sidebar__switcher-title">{{ $ml.get('editMemorial.sidebar.main.showQuote.title') }}</div>
                  <div class="memorial-sidebar__switcher-wrapper">
                      <!-- при клике добавлять и убирать класс - switch -->
                      <div class="memorial-sidebar__switcher-box"

                           :class="{'switch': !model.showQuote}"
                           @click="model.showQuote=!model.showQuote;save('showQuote')"
                      >
                          <div class="memorial-sidebar__switcher-point"></div>
                      </div>
                      <span>{{ model.showQuote ? $ml.get('editMemorial.sidebar.main.showQuote.yes') : $ml.get('editMemorial.sidebar.main.showQuote.no') }}</span>
                  </div>
              </div>
          </div>
      </div>

  </div>
</template>

<script>
  import Vue from 'vue';
  import Datepicker from 'vuejs-datepicker';
  import {he, en} from 'vuejs-datepicker/dist/locale'
  import {mapGetters} from "vuex";
  import SimpleVueValidation from 'simple-vue-validator';

  const Validator = SimpleVueValidation.Validator;

  Vue.use(SimpleVueValidation);

  export default {
  name: "Main",
  components: {
    Datepicker,
  },
  props: {
    memorial: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      dtLocale: he,
      birthDateModPh: false,
      selectedAvatar: false,
      avatarPreview: null,
      birthDate: null,
      deathDate: null,
      model: {
        name: null,
        birthDate: null,
        deathDate: null,
        profilePhotoId: null,
        profilePhotoFullScreen: true,
        showQuote: true,
      },
      covers: [
        {
          id: '1',
          name: 'האוקיינוס',
          file: require('@/assets/img/full-card-bg2.png'),
        },
        {
          id: '2',
          name: 'פרחים',
          file: require('@/assets/img/memorial-style-item-2.png'),
        },
        {
          id: '3',
          name: 'טרסות',
          file: require('@/assets/img/memorial-style-item-3.png'),
        },
        {
          id: '4',
          name: 'שמים',
          file: require('@/assets/img/memorial-style-item-4.png'),
        },
        {
          id: '5',
          name: 'נחלים',
          file: require('@/assets/img/memorial-style-item-5.png'),
        },
      ]
    }
  },
    validators: {
      'model.name': function (value) {
        return Validator.value(value).required().minLength(1).maxLength(254).regex('^(\\w+?|\\d+?|\\s+?|[`\'\"()-_=+*!?\u0590-\u05FF]+?)+$');
      },
      'model.deathDate': function (value) {
        return Validator.value(value).required();
      },
      'model.birthDate': function (value) {
        return Validator.value(value).required();
      },
    },
  computed: {
    ...mapGetters({
      errors: 'memorial/getErrors',
      formIsLoading: 'memorial/formIsLoading',
      uploadedFile: 'memorial/getFile',
    }),
    isIOS() {
      return /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
    },
  },
  mounted() {
    this.$store.commit('memorial/setErrors');

    this.model.name = this.memorial.name;
    this.model.deathDate = this.memorial.deathDate;
    this.model.birthDate = this.memorial.birthDate;
    this.model.profilePhotoId = this.memorial.profilePhoto ? this.memorial.profilePhoto.id : null;
    this.model.profilePhotoFullScreen = this.memorial.profilePhotoFullScreen;
    this.model.profilePhotoBackground = this.memorial.profilePhotoBackground;
    this.model.showQuote = this.memorial.showQuote;

    if(!this.model.profilePhotoBackground) {
      this.model.profilePhotoBackground = '1';
      this.save('profilePhotoBackground');
    }

    this.avatarPreview = this.memorial.profilePhoto ? this.memorial.profilePhoto.url : require('@/assets/img/reserve-bg.png');
    this.deathDate = this.$dateUtils.formatDate(new Date(this.model.deathDate), 'dd MMM yyyy', '-', this.$ml.current);
    this.birthDate = this.$dateUtils.formatDate(new Date(this.model.birthDate), 'dd MMM yyyy', '-', this.$ml.current);
  },
  methods: {
    async save(field) {
      this.$store.commit('memorial/setErrors');

      const payload = {
        memorialId: this.memorial.id,
        data: {
          'editVersion': this.memorial.editVersion,
          [field]: this.model[field],
        },
      };

      let $this = this;

      this.$validate()
        .then(async function (success) {
          // console.log(success);

          if (success) {
            await $this.$store.dispatch('dashboard/updateFields', payload);
            $this.$emit('updated');
          }
        });
    },
    handleKeyDown(e) {
      if (this.isUpdated) {
        e.preventDefault();
        return false;
      }

      return true;
    },
    bindClassInvalid(key) {
      return this.errors[key] || this.validation.hasError(`model.${key}`);
    },
    showErrorText(key) {
      return this.errors[key] || this.validation.firstError(`model.${key}`);
    },
    closeBirthdayPicker() {
      this.$refs.birthday.close();
      this.save('birthDate');
    },
    openBirthdayPicker() {
      this.$refs.birthday.showCalendar();
    },
    birthdayPickerSelected(value) {
      this.model.birthDate = this.$dateUtils.formatDate(value, 'yyyy-MM-dd',  '-', this.$ml.current);
      this.birthDate = this.$dateUtils.formatDate(value, 'dd MMM yyyy',  '-', this.$ml.current);
    },
    closeDeathPicker() {
      this.$refs.death.close();
      this.save('deathDate');
    },
    openDeathPicker() {
      this.$refs.death.showCalendar();
    },
    deathPickerSelected(value) {
      this.model.deathDate = this.$dateUtils.formatDate(value, 'yyyy-MM-dd', '-', this.$ml.current);
      this.deathDate = this.$dateUtils.formatDate(value, 'dd MMM yyyy', '-', this.$ml.current);
    },
    async uploadAvatar() {
      this.selectedAvatar = true;
      let file = Array.from(this.$refs.avatar.files)[0];

      if (file) {
        const fileData = {
          file: file,
          type: 'image',
        };

        await this.$store.dispatch('memorial/upload', fileData);

        if(this.uploadedFile) {
          this.avatarPreview = URL.createObjectURL(file);
          this.model.profilePhotoId = this.uploadedFile.id;

          await this.save('profilePhotoId');
        }
      }
    },
  }
}
</script>

<style lang="scss">

  .datepicker {
    input {
      position: absolute !important;
      opacity: 0;
    }
  }

  .vdp-datepicker {
      &__calendar {
          width: 100% !important;
      }
  }
</style>